import api from '@/common/api.js'
import { Config } from '@/config/config.js'
import displayItem from '@/components/display-item.vue';
export default {
	components: { displayItem },
	data() {
		return {
			login: false,
			headerUrl: '/static/images/account/person-bg.jpg',
			info: {},
			member: null
		}
	},
	onLoad(e) {
		if (getApp().globalData.token) this.login = true;
		else this.login = false;
		this.load();
	},
	computed: {
		getAvatarIcon() {
			return Config.baseUrl + this.info.avatarImage;
		}
	},
	methods: {
		load() {
			this.getAccount();
			this.getUserMember();
		},
		getAccount() {
			api.getAccount().then((result)=>{
				if (result.code == 0) {
					console.log('getUserInfo ', result);
					let data = result.data;
					let phone = '';
					if (data.phone) {
						phone = data.phone.substr(0, 3) + '****' + data.phone.substr(data.phone.length-4, 4);
					} else {
						phone = ' ';
					}
					data.phone = phone;
					this.info = data;
				}
			})
		},
		getUserMember() {
			api.getUserMember({
				courseId: getApp().globalData.courseId
			}).then((result)=>{
				console.log('getUserMember ', result);
				if (result.code == 0) {
					if (result.data) this.member = result.data;
				}
			});
		},
		goLogin() {
			if (!this.login) {
				uni.navigateTo({
					url: '/pages/account/login'
				});
			}
		},
		goBalance() {
			console.log('goBalance');
		},
		goAbout() {
			uni.navigateTo({
				url: '/pages/about/about'
			});
		},
		
		goSetting() {
			uni.navigateTo({
				url: '/pages/account/setting'
			});
		},
		onPress(type) {
			switch (type) {
				case 0:	// 个人信息
				break;
				case 1:	// 账户
				break;
				case 2:	// 已购试卷
				break;
				case 3:	// 礼券
				break;
				case 4:
				case 8:
					uni.showToast({
						title: '程序小哥正在快马加鞭，敬请期待噢~',
						icon: 'none'
					});
					break;
				case 5:	// 笔记
				break;
				case 6:	// 留言
				break;
				case 7: // 收藏
				break;
				case 9:	// 做题记录
				break;
				case 10:	// 错题库
				break;
				
			}
		}
	}
}
